import {
    EVENT_FLAG_ATTACK,
    EVENT_FLAG_WINN,
    PieceIndex,
} from '../ren';
import EventHandler, { ListenerType } from './EventHandler';

export class BoardEvent {
    flag: string;
    actorPieceIndex: PieceIndex;
    get isAttack() {
        return this.flag === EVENT_FLAG_ATTACK;
    }
    get isWin() {
        return this.flag === EVENT_FLAG_WINN;
    }
    constructor({ flag, actorPieceIndex }:
        { flag: string, actorPieceIndex: PieceIndex }) {
        this.flag = flag;
        this.actorPieceIndex = actorPieceIndex;
    }
}
export default class BoardEventController extends EventHandler {
    static EVENT = 'event';
    constructor() {
        super({
            events: {
                EVENT: BoardEventController.EVENT,
            },
        });
    }
    fireEvent(boardEvent: BoardEvent) {
        this._addPropEvent(BoardEventController.EVENT, boardEvent);
    }
    addBoardEventListener(listener: ListenerType<BoardEvent>) {
        this._addOnEventListener(BoardEventController.EVENT, listener);
    }
    removeBoardEventListener(listener: ListenerType<BoardEvent>) {
        this._removeOnEventListener(BoardEventController.EVENT, listener);
    }
}
